import * as C from '../types';

const initialState = {
	clientsFieldDescription: '',
	description: '',
	peoplesText: 'People currently we have in the team',
	projectCount: 100,
	projectsText: 'Projects we developed',
	sectorFieldDescription:
		'We have developed numerous projects in different sectors',
	sectorFieldName: 'Domain Sector',
	services: [],
	slogan: '',
	years: 11,
	yearsText: '',
	_id: 0,
};

export const HomeInfo = (state = initialState, action) => {
	switch (action.type) {
		case C.FETCH_HOME_INFO:
			return { ...state, isFetching: true };
		case C.FINISHED_FETCH_HOME_INFO:
			return { ...state, isFetching: false, payload: action.data };
		case C.ERROR_FETCH_HOME_INFO:
			return {
				...state,
				payload: [],
				isFetching: false,
				error: action.error,
			};
		default:
			return state;
	}
};

export default HomeInfo;
